import DataLoadingModel from '../models/DataLoading';
import { DataLoadingEstab } from '../types';

const dataLoading = new DataLoadingModel();

export default class DataLoadingController {
  public async getEstab(estab: number) {
    return dataLoading.estabelecimento(estab);
  }

  public async getSetor() {
    return dataLoading.setor();
  }

  public async getClientes() {
    return dataLoading.Clientes();
  }

  public async getMotivo() {
    return dataLoading.Motivo();
  }

  public async getCor(data: { ESB_COD: number; OS_NUMER: number }) {
    return dataLoading.Cor(data);
  }
}
