import { Router } from 'express';
import { DuplicatasAberto } from '../types';
import FinancesController from '../controllers/FinancesController';
import ValidateToken from '../middlewares/ValidadeToken';

const financesRoutes = Router();
const financesController = new FinancesController();

financesRoutes.get('/dupAberto', async (request, response) => {
  const data = request.query as unknown;
  const responseData = await financesController.getDuplicatasAberto(
    data as DuplicatasAberto,
  );

  const statusCode = responseData.length === 0 ? 404 : 200;

  response.status(statusCode).json(responseData);
});

financesRoutes.get('/dupAtraso', async (request, response) => {
  const data = request.query as unknown;
  const responseData = await financesController.getDuplicataAtraso(
    data as DuplicatasAberto,
  );

  const statusCode = responseData.length === 0 ? 404 : 200;

  response.status(statusCode).json(responseData);
});

export default financesRoutes;
