import FinanceModel from '@models/Finances';
import { DuplicatasAberto } from 'src/types';

const dataFinances = new FinanceModel();

export default class DataLoadingController {
  public async getDuplicatasAberto(data: DuplicatasAberto) {
    const dataFormatted = {
      estab: data.estab,
      cliIni: data.cliIni || 1,
      cliFin: data.cliFin || 999999,
      dataIni: data.dataIni || new Date(),
      dataFini: data.dataFini || new Date(),
    };

    return dataFinances.duplicasAberto(dataFormatted);
  }

  public async getDuplicataAtraso(data: DuplicatasAberto) {
    const dataFormatted = {
      estab: data.estab,
      cliIni: data.cliIni || 1,
      cliFin: data.cliFin || 999999,
      dataIni: data.dataIni || new Date(),
      dataFini: data.dataFini || new Date(),
    };

    return dataFinances.duplicatasAtraso(dataFormatted);
  }
}
