import { Router } from 'express';
import ProductionController from '../controllers/ProdutionController';
import ValidateToken from '../middlewares/ValidadeToken';
import {
  AppontData,
  AppontDataEtq,
  GetDataOS,
  GetDataOSAppontEtq,
  GetValidateOS,
} from '../types';

const produtionController = new ProductionController();
const productionRouter = Router();

productionRouter.get('/listOS', ValidateToken, async (request, response) => {
  const data = request.query as unknown;

  response.json(await produtionController.getDataOs(data as GetDataOS));
});

productionRouter.get(
  '/listOSAppont',
  // ValidateToken,
  async (request, response) => {
    const data = request.query as unknown as GetDataOSAppontEtq;

    response.json(await produtionController.getDataOSAppont(data));
  },
);

productionRouter.get(
  '/validateOS',
  ValidateToken,
  async (request, response) => {
    const data = request.query as unknown;
    response.json(
      await produtionController.getValidateOS(data as GetValidateOS),
    );
  },
);

productionRouter.post(
  '/createAppont',
  ValidateToken,
  async (request, response) => {
    const data = request.body;

    produtionController.createAppont(data as AppontData[], request.user);

    response.status(200).end();
  },
);

productionRouter.post(
  '/createAppontEtq',
  ValidateToken,
  async (request, response) => {
    const data = request.body as unknown as AppontDataEtq[];

    produtionController.CreateAppontEtq(data as AppontDataEtq[], request.user);

    response.status(200).end();
  },
);

productionRouter.get(
  '/appontHome',
  ValidateToken,
  async (request, response) => {
    return response.json(
      await produtionController.getAppontHome(request.user.data.COD_SETOR),
    );
  },
);

export default productionRouter;
