import { Router } from 'express';
import ValidateToken from '../middlewares/ValidadeToken';

const appRoutes = Router();

appRoutes.get('/', (request, response) => {
  if (request.cookies.token) {
    return response.redirect('/home');
  }

  return response.render('index', { auth: true });
});

appRoutes.get('/home', ValidateToken, (request, response) => {
  response.render('home', {
    user: {
      LOGIN:
        request.user.data.LOGIN[0].toUpperCase() +
        request.user.data.LOGIN.substr(1).toLowerCase(),
      SETOR:
        request.user.data.SETOR[0].toUpperCase() +
        request.user.data.SETOR.substr(1).toLowerCase(),
    },
  });
});

