import { UserParameters } from '../types';
import connection from '../database/connection';

export default class Signin {
  public async create({ user, pass }: UserParameters): Promise<{}> {
    const conn = await connection();

    const query = `
        SELECT
          C.SET_DESC SETOR,
          C.SET_COD COD_SETOR,
          B.USU_NOME LOGIN,
          B.ESB_COD ESTAB
        FROM
          SINUSUARIOS A, SINUSUSETOR B, SINSETOR C
        WHERE
          (B.USU_NOME = '${user}' AND A.USU_SENHA = '${pass}') AND
          A.USU_NOME = B.USU_NOME AND
          B.SET_COD = C.SET_COD
    `;

    const data = await conn.execute(query);

    if (conn) await conn.close();

    return data.rows[0];
  }
}
