import connection from '../database/connection';
import {
  DataLoadingEstab,
  DataLoadingSetor,
  DataLoadingCliente,
  DataLoadingMotivo,
} from '../types';

export default class DataLoading {
  public async estabelecimento(estab: number) {
    const data = (
      await (
        await connection()
      ).execute(`SELECT ESB_COD, ESB_DESC FROM SINESTAB ORDER BY ESB_COD`)
    ).rows;

    return data as DataLoadingEstab[];
  }

  public async setor() {
    const data = (
      await (
        await connection()
      ).execute('SELECT SET_COD, SET_DESC FROM SINSETOR')
    ).rows;

    return data as DataLoadingSetor[];
  }

  public async Clientes() {
    const data = (
      await (
        await connection()
      ).execute(
        'SELECT CLI_COD, CLI_RAZAO_SOC FROM SINCLIENTES ORDER BY CLI_COD',
      )
    ).rows;

    return data as DataLoadingCliente[];
  }

  public async Motivo() {
    const data = (
      await (
        await connection()
      ).execute(
        "SELECT MOC_COD, MOC_DESC FROM SINMOTIVO_OCORRENCIA WHERE MOC_ATIVO = 'S' ORDER BY MOC_COD",
      )
    ).rows;

    return data as DataLoadingMotivo[];
  }

  public async Cor(data: { ESB_COD: number; OS_NUMER: number }) {
    const conn = await connection();

    const query = `
      SELECT ESB_COD, OS_NUMER, A.ITE_COD, A.ITE_COD||' - '||B.ITE_DESC DESC_ITEM
      FROM SINOSITEM_INDUSTR A, SINITEM B
      WHERE A.ESB_COD  = ${data.ESB_COD} AND
            A.OS_NUMER = ${data.OS_NUMER} AND
            A.TIP_COD  = B.TIP_COD AND
            A.ITE_COD  = B.ITE_COD
      GROUP BY ESB_COD, OS_NUMER, A.ITE_COD, B.ITE_DESC
    `;

    const result = await conn.execute(query);

    return result.rows;
  }
}
