import { Router } from 'express';
import DataLoadingController from '../controllers/DataLoadingController';
import ValidateToken from '../middlewares/ValidadeToken';

const loadRoutes = Router();
const sessionController = new DataLoadingController();

loadRoutes.get('/estab', ValidateToken, async (request, response) => {
  const { ESTAB } = request.user.data;

  const data = await sessionController.getEstab(ESTAB);
  response.json(data);
});

loadRoutes.get('/setor', async (_, response) => {
  const data = await sessionController.getSetor();
  response.json(data);
});

loadRoutes.get('/clientes', async (_, response) => {
  const data = await sessionController.getClientes();
  response.json(data);
});

loadRoutes.get('/motivo', async (_, response) => {
  const data = await sessionController.getMotivo();
  response.json(data);
});

loadRoutes.get('/cor', async (request, response) => {
  const data = request.query as unknown as {
    ESB_COD: number;
    OS_NUMER: number;
  };

  return response.json(await sessionController.getCor(data));
});
export default loadRoutes;
