import InsertLog from 'src/database/sqlite3.connection';
import ProductionModel from '../models/Production';
import AppError from '../errors/AppError';
import {
  AppontData,
  GetDataOS,
  GetValidateOS,
  UserPayload,
  GetDataOSAppontEtq,
  AppontDataEtq,
} from '../types';

const productionModel = new ProductionModel();

export default class ProductionController {
  public async getDataOs(data: GetDataOS) {
    if (!data.Estab || !data.OS || !data.Setor || !data.Cor) {
      throw new AppError('Parametros Informados Incorretos');
    }

    const result = await productionModel.getDataOS(data);
    return result;
  }

  public async getDataOSAppont(data: GetDataOSAppontEtq) {
    if (!data.Estab || !data.OS || !data.Setor || !data.cor) {
      throw new AppError('Parametros Informados Incorretos');
    }

    const result = await productionModel.getDataOSAppontEtq(data);
    return result;
  }

  public async getValidateOS(data: GetValidateOS) {
    if (!data.Estab || !data.OS) {
      throw new AppError('Parametros Informados Incorretos');
    }

    const result = await productionModel.getValidateOS(data);
    return result;
  }

  public async createAppont(data: AppontData[], user: UserPayload) {
    if (data.length === 0) {
      throw new AppError('Parametros Informados Incorretos');
    }

    await InsertLog({
      params: JSON.stringify(data),
      user: user.data.LOGIN,
      date: new Date().toLocaleString(),
      type: 'insert',
      resource: 'Cria√ß√£o de Apontamento',
    });

    return productionModel.CreateAppont(data, user);
  }

  public CreateAppontEtq(data: AppontDataEtq[], user: UserPayload) {
    if (data.length === 0) {
      throw new AppError('Parametros Informados Incorretos');
    }

    return productionModel.CreateAppontEtq(data, user);
  }

  public async getAppontHome(setor: number) {
    return productionModel.getAppontHome(setor);
  }
}
