import Signin from '@models/Signin';
import md5 from 'md5';
import jwt from 'jsonwebtoken';
import Auth from '@config/auth';
import AppError from '@errors/AppError';
import { UserParameters } from '../types';

const signinModel = new Signin();

export default class SessionController {
  public async logon(params: UserParameters): Promise<{
    token: string;
    user: string;
  }> {
    if (!params.user || !params.pass) {
      throw new AppError('Usu√°rio ou Senha Obrigat√≥rios', 400);
    }

    const data = {
      user: params.user.toLocaleUpperCase(),
      pass: md5(params.pass.toLocaleUpperCase()).toLocaleUpperCase(),
    };

    const userlogged = await signinModel.create(data);

    if (!userlogged) {
      throw new AppError('Usu√°rio ou Senha Incorretos! Tente Novamente.', 400);
    }

    const { secret, expiresIn } = Auth.jwt;

    const token = jwt.sign({ data: userlogged }, secret, { expiresIn });

    return {
      token,
      user: params.user,
    };
  }
}
