import { Router } from 'express';
import Sessions from './session.routes';
import View from './view.routes';
import Load from './load.routes';
import Production from './production.routes';
import Finances from './finances.routes';

const routes = Router();

routes.use('/api/sessions', Sessions);
routes.use('/api/loads', Load);
routes.use('/api/production', Production);
routes.use('/api/finances', Finances);
routes.use('/', View);

export default routes;
